<template>
      <!-- 添加购物车 -->
  <add-cart ref="addCart"> </add-cart>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
  name: "addCartView",
  data() {
    return {

    };
  },
  computed: {...mapGetters(['proDetail'])},
  mounted() {
    
  },
  watch:{
    'proDetail':function(val){
      if(val){
      console.log(this.proDetail.spuCode)
           this.$refs.addCart.load(this.proDetail,true);
      }
    }
  }
}

</script>


